@import '../../styles/index.scss';

.ViewHeaderTitle{
    text-align: center;
    color: $accent-color;
    font-size: 4.2vw;
    font-weight: 300;
    width: 100%;
}

.ViewHeaderBlurb{
    color: $secondary-color;
    font-size: 1.5vw;
    font-weight: 600;
    display: block;
    @include MarginTopBottom(3vh);
    margin-left: auto;
    margin-right: auto;
}

.ViewHeaderImageContainer{
    @include MarginTopBottom(4vh);
    text-align: center;
    position: relative;
    border: 3px solid $secondary-color;
    border-radius: 5px;
    height: 60vh;
    filter: grayscale(0.6);
}

.ViewHeaderImage{
    width: 100%;
    height: 100%;
    opacity: 0.8;
}
